import { Controller } from '@ali/4ever-cangjie';
import { Marks } from '@ali/4ever-mo';
import { addMarkAtCurrentRange }  from '@ali/4ever-utils';
import { removeMarkAtCurrentRange }  from '@ali/4ever-utils';

export default function setFonts(controller: Controller, fonts) {
  if (fonts === 'default') {
    // 清除字体信息
    return removeMarkAtCurrentRange(controller, 'fonts');
  }
  const mark = Marks.Fonts.create({
    ascii: fonts,
    hAnsi: fonts,
    cs: fonts,
    eastAsia: fonts,
  });
  return addMarkAtCurrentRange(controller, mark);
}
